import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { Sim001Component } from './sim001/sim001.component';
import { Sim002Component } from './sim002/sim002.component';
import { SecurityAccessGuard } from '../../common/guards/security-access.guard';

const routes: Routes = [
  {
    path: 'sim001',
    component: Sim001Component,
    canActivate: [SecurityAccessGuard],
    canDeactivate: [SecurityAccessGuard],
    data: {
      stepId: 'CRE001'
    }
  },
  {
    path: 'sim002',
    component: Sim002Component,
    canActivate: [SecurityAccessGuard],
    canDeactivate: [SecurityAccessGuard],
    data: {
      stepId: 'CRE002'
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SimuladorRoutingModule { }
