import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { ValidationRulesService } from 'src/app/modules/common/service/validationRules/validation-rules.service';
import { Errores } from './errores';
import { CurrencyPipe } from '@angular/common';

@Component({
  selector: 'app-sim001-form',
  templateUrl: './sim001-form.component.html',
  styleUrls: ['./sim001-form.component.scss']
})
export class Sim001FormComponent implements OnInit {

  regexComa = /\,/g;
  dataPayload: any;
  errores: any;
  ready: boolean;
  @Input() formulario: FormGroup;
  @Input() rules: ValidationRulesService;
  @Input() simbol: string;
  // tslint:disable-next-line: no-inferrable-types
  @Input() pais: string = '';
  @Input() set payload(data) {
    this.dataPayload = data;
    this.ready = true;
  }
  @Output() data: EventEmitter<any> = new EventEmitter();

  constructor() { }

  ngOnInit() {
    if ( this. pais === 'SV' ) {
      this.regexComa = /\./g;
    }
    this.errores = new Errores().get(this.rules);
  }

  mascaraNumeros(value: string): string {
    return new CurrencyPipe('en-US').transform(value, 'USD', 'L ', '.2');
  }

}
