import { Component, OnInit, OnDestroy } from '@angular/core';
import { BaseComponent } from 'src/app/modules/common/components/base/base.component';
import { Payload, BackButton } from 'src/app/modules/common/components/base/base';
import { ViewEncapsulation } from 'src/app/modules/common/components/encaptulation';
import { PAIS, MODULO, LENGUAJE } from 'src/app/modules/common/CONST';
import { forkJoin } from 'rxjs';

@Component({
  selector: 'app-sim001',
  templateUrl: './sim001.component.html',
  styleUrls: ['./sim001.component.scss', '../../../../../assets/cam/scss/components/simulador/_sim001.scss'],
  encapsulation: ViewEncapsulation.ShadowDom
})
export class Sim001Component extends BaseComponent implements OnInit, OnDestroy, Payload, BackButton {

  countrieRules: {[key: string]: any};
  showForm: boolean;
  // Data del pais en cuestion
  data: any;

  rangos: {
    noCliente: { maxValorDeseado: number; minValorDeseado: number; intervalo: number; };
    cliente: { maxValorDeseado: number; minValorDeseado: number; intervalo: number; }
  };

  plazo: {
    cliente: { minPlazo: number; maxPlazo: number; intervalo: number; };
    noCliente: { minPlazo: number; maxPlazo: number; intervalo: number; };
  };

  ngOnInit() {
    this.rangos = {
      noCliente: { maxValorDeseado: 0, minValorDeseado: 0, intervalo: 0 },
      cliente: { maxValorDeseado: 0, minValorDeseado: 0, intervalo: 0 }
    };
    this.plazo = {
      cliente: { minPlazo: 0, maxPlazo: 0, intervalo: 0 },
      noCliente: { minPlazo: 0, maxPlazo: 0, intervalo: 0 }
    };
    const tempPais = this.pais.toString();
    this.pais = '';
    this.showForm = false;
    forkJoin(
      this.catalogo.data('CRE001_PLAZO', this.params([PAIS, MODULO, LENGUAJE])),
      this.catalogo.data('CRE001_RANGO', this.params([PAIS, MODULO, LENGUAJE]))
    ).subscribe(
      response => {
        this.rangos = response[1][0];
        this.plazo = response[0][0];
        this.countrieRules = {
          CR: () => this.setRulesCRPASV(),
          HN: () => this.setRulesHN(),
          SV: () => this.setRulesCRPASV(),
          PA: () => this.setRulesCRPASV()
        };
        if ( this.countrieRules[tempPais] ) {
          this.countrieRules[tempPais]();
        }
        this.pais = tempPais;
        this.showForm = true;
        }
    );
  }

  ngOnDestroy(): void {
    // throw new Error('Method not implemented.');
  }

  setData(data: any): void {
    this.data = data;
  }

  getDataToPayload(data?: any): () =>  ({ [key: string]: any; }) {
    this.disableButton = true;
    return () => this.getPayload();
  }

  getDataToBackButton(data?: any): () => ({ [key: string]: any; }) {
    // throw new Error('Method not implemented.');
    return () => ({});
  }

  getPayload(): any {
    let payload = this.formulario.getRawValue();
    if ( this.pais === 'CR') {
      payload = {
        ingresosMensuales: '',
        valorDeseado: payload.valorDeseado * this.rangos[this.payload.esCliente ? 'cliente' : 'noCliente'].intervalo,
        plazo: payload.plazo * this.plazo[this.payload.esCliente ? 'cliente' : 'noCliente'].intervalo,
        creditoLibranza: payload.creditoLibranza
      };
    }
    if ( this.pais === 'PA') {
      payload = {
        ingresosMensuales: '',
        valorDeseado: payload.valorDeseado * this.rangos[this.payload.esCliente ? 'cliente' : 'noCliente'].intervalo,
        plazo: payload.plazo * this.plazo[this.payload.esCliente ? 'cliente' : 'noCliente'].intervalo,
        creditoLibranza: payload.creditoLibranza
      };
    }
    if ( this.pais === 'SV') {
      payload = {
        ingresosMensuales: '',
        valorDeseado: payload.valorDeseado * this.rangos[this.payload.esCliente ? 'cliente' : 'noCliente'].intervalo,
        plazo: payload.plazo * this.plazo[this.payload.esCliente ? 'cliente' : 'noCliente'].intervalo,
        creditoLibranza: payload.creditoLibranza
      };
    }
    if ( this.pais === 'HN') {
      payload = {
        ingresosMensuales: payload.ingresosMensuales,
        valorDeseado: payload.valorDeseado * this.rangos[this.payload.esCliente ? 'cliente' : 'noCliente'].intervalo,
        plazo: payload.plazo * this.plazo[this.payload.esCliente ? 'cliente' : 'noCliente'].intervalo,
        creditoLibranza: payload.creditoLibranza
      };
    }
    return payload;
  }

  setRulesCRPASV(): void {
    this.setRules({
      valorDeseado: [this.rangos[this.payload.esCliente ? 'cliente' : 'noCliente'].minValorDeseado /
                     this.rangos[this.payload.esCliente ? 'cliente' : 'noCliente'].intervalo, [
        this.rules.validationForRequired(),
        this.rules.validationForMaxNumber(
          this.rangos[this.payload.esCliente ? 'cliente' : 'noCliente'].maxValorDeseado /
          this.rangos[this.payload.esCliente ? 'cliente' : 'noCliente'].intervalo
        ),
        this.rules.validationForMinNumber(
          this.rangos[this.payload.esCliente ? 'cliente' : 'noCliente'].minValorDeseado /
          this.rangos[this.payload.esCliente ? 'cliente' : 'noCliente'].intervalo
        )
      ]],
      plazo: [this.plazo[this.payload.esCliente ? 'cliente' : 'noCliente'].minPlazo /
              this.plazo[this.payload.esCliente ? 'cliente' : 'noCliente'].intervalo, [
        this.rules.validationForRequired(),
        this.rules.validationForMaxNumber(
          this.plazo[this.payload.esCliente ? 'cliente' : 'noCliente'].maxPlazo /
          this.plazo[this.payload.esCliente ? 'cliente' : 'noCliente'].intervalo
        ),
        this.rules.validationForMinNumber(
          this.plazo[this.payload.esCliente ? 'cliente' : 'noCliente'].minPlazo /
          this.plazo[this.payload.esCliente ? 'cliente' : 'noCliente'].intervalo
        )
      ]],
      ingresosMensuales: ['', []],
      creditoLibranza: [false, []]
    });
  }

  setRulesHN(): void {
    this.setRules({
      valorDeseado: [this.rangos[this.payload.esCliente ? 'cliente' : 'noCliente'].minValorDeseado /
                     this.rangos[this.payload.esCliente ? 'cliente' : 'noCliente'].intervalo, [
        this.rules.validationForRequired(),
        this.rules.validationForMaxNumber(
          this.rangos[this.payload.esCliente ? 'cliente' : 'noCliente'].maxValorDeseado /
          this.rangos[this.payload.esCliente ? 'cliente' : 'noCliente'].intervalo
        ),
        this.rules.validationForMinNumber(
          this.rangos[this.payload.esCliente ? 'cliente' : 'noCliente'].minValorDeseado /
          this.rangos[this.payload.esCliente ? 'cliente' : 'noCliente'].intervalo
        )
      ]],
      plazo: [this.plazo[this.payload.esCliente ? 'cliente' : 'noCliente'].minPlazo /
              this.plazo[this.payload.esCliente ? 'cliente' : 'noCliente'].intervalo, [
        this.rules.validationForRequired(),
        this.rules.validationForMaxNumber(
          this.plazo[this.payload.esCliente ? 'cliente' : 'noCliente'].maxPlazo /
          this.plazo[this.payload.esCliente ? 'cliente' : 'noCliente'].intervalo
        ),
        this.rules.validationForMinNumber(
          this.plazo[this.payload.esCliente ? 'cliente' : 'noCliente'].minPlazo /
          this.plazo[this.payload.esCliente ? 'cliente' : 'noCliente'].intervalo
        )
      ]],
      ingresosMensuales: ['', [
        // this.rules.validationForPattern(/^\d{4,7}(\.\d{1,2})?$/),
        this.rules.validationForRequired(),
        this.rules.validationForMinLength(4),
        this.rules.validationForMaxLength(7)
      ]],
      creditoLibranza: [false, []]
    });
  }

  getCatalogo(KEY: string, valor: (valor) => void): void {
    this.catalogo.data(KEY, this.params([PAIS, MODULO, LENGUAJE])).subscribe( response => {
      valor(response[0]);
    });
  }
}
