import { Component, OnInit, OnDestroy } from '@angular/core';
import { BaseComponent } from 'src/app/modules/common/components/base/base.component';
import { Payload, BackButton } from 'src/app/modules/common/components/base/base';
import { ViewEncapsulation } from 'src/app/modules/common/components/encaptulation';
import { LENGUAJE, MODULO, PAIS } from 'src/app/modules/common/CONST';

@Component({
  selector: 'app-sim002',
  templateUrl: './sim002.component.html',
  styleUrls: ['./sim002.component.scss', '../../../../../assets/cam/scss/components/simulador/_sim002.scss'],
  encapsulation: ViewEncapsulation.ShadowDom
})
export class Sim002Component extends BaseComponent implements OnInit, OnDestroy, Payload, BackButton {

  messageTooltip: string;
  messageFooter: string;

  ngOnInit() {
    this.catalogo.data('MSG_RESULTADO_SIMULACION', this.params([PAIS, MODULO, LENGUAJE])).subscribe( response => {
      this.messageFooter = response[0] ? response[0].pieDePagina : '';
      this.messageTooltip = response[0] ? response[0].tooltip : '';
    });
  }

  ngOnDestroy(): void {
    // throw new Error('Method not implemented.');
  }

  getDataToPayload(data?: any): () => ({ [key: string]: any; }) {
    this.disableButton = true;
    return () => ({
      nuevaSimulacion: !data
    });
  }

  getDataToBackButton(data?: any): () => ({ [key: string]: any; }) {
    // throw new Error('Method not implemented.');
    return () => ({});
  }
}
