import { ValidationRulesService } from 'src/app/modules/common/service/validationRules/validation-rules.service';

export class Errores {
  get(rules: ValidationRulesService): any {
    return {
      ingresosMensuales: [
        { type: rules.errorTypeRequired, label: 'Información requerida.'},
        {
          type: rules.errorTypePattern,
          label: ' Formato incorrecto, se permiten 4 digitos minitmos y 7 maximos, con 2 decimales opcionales.'
        },
        { type: rules.errorTypeMinLength, label: 'No se permiten menos de 4 digitos.'},
        { type: rules.errorTypeMaxLength, label: 'No se permiten más de 7 digitos.'},
      ],
      valorDeseado: [],
      plazo: [],
      creditoLibranza: []
    };
  }
}
