import { Component, OnInit, Input, forwardRef, ViewEncapsulation } from '@angular/core';
import { NG_VALUE_ACCESSOR, ControlValueAccessor } from '@angular/forms';

@Component({
  selector: 'app-input-range',
  templateUrl: './input-range.component.html',
  styleUrls: ['./input-range.component.sass'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => InputRangeComponent),
      multi: true
    }
  ],
  encapsulation: ViewEncapsulation.None
})
export class InputRangeComponent implements OnInit, ControlValueAccessor {
  /*
    tslint:disable-next-line: no-input-rename
    tslint:disable-next-line: variable-name
  */
  @Input('value') _value: number;
  @Input() label = '';
  @Input() symbol = '';
  @Input() rangeTextClass = '';
  @Input() regex: string;
  @Input() pais: string;
  @Input() interval: number;
  @Input() minValue: number;
  @Input() maxValue: number;

  onChange: any = () => {};
  onTouched: any = () => {};

  constructor() { }

  ngOnInit() {
  }

  get value() {
    return this._value;
  }

  set value(value) {
    this._value = value;
    this.onChange(value);
    this.onTouched();
  }

  writeValue(value) {
    this._value = value;
  }

  registerOnTouched(fn) {
    this.onTouched = fn;
  }

  registerOnChange(fn) {
    this.onChange = fn;
  }

}
